.advantage-one,
.advantage-two,
.advantage-three,
.advantage-four {
  width: 320px;
  margin: 0 auto;
}

.advantage-one__city,
.advantage-three__bridge {
  background-color: #81b3d2;
}

.advantages h2,
.advantages h3,
.advantages p {
  margin: 0px;
  text-transform: uppercase;
  text-align: center;
}

.advantage-one__title,
.advantage-one__number,
.advantage-one__description,
.advantage-three__title,
.advantage-three__number,
.advantage-three__description {
  color: white;
}

.advantage-one__title,
.advantage-three__title {
  padding: 60px 15px 14px;

  font-size: 17px;
  letter-spacing: -0.6px;
}

.advantage-three__title {
  padding-top: 41px;
}

.advantage-one__number,
.advantage-three__number,
.advantage-one__description,
.advantage-three__description,
.advantage-four__number {
  font-size: 14px;
}

.advantage-one__number,
.advantage-three__number {
  padding-right: 55px;
  padding-bottom: 18px;
  padding-left: 55px;
}

.advantage-one__description,
.advantage-three__description {
  padding: 0px 26px 39px;
  
  letter-spacing: -0.9px;
  line-height: 21px;
}

.avantage-one__photo,
.advantage-three__photo {
  font-size: 0px;
}

.advantage-two {
  background-color: white;
}

.advantage-two .advantage-two__title,
.advantage-two .advantage-two__description {
  text-align: left;
}

.advantage-two__title {
  padding: 32px 70px 0px;

  letter-spacing: -1.5px;
}

.advantage-two__title,
.advantage-four__title {
  font-size: 18px;
}

.advantage-two__title {
  position: relative;

  &::before {
    content: "";

    position: absolute;

    background-repeat: no-repeat;
  }
}

.advantage-two__left-title::before {
  top: 27px;
  left: 21px;

  width: 39px;
  height: 37px;

  background-image: url(../img/icon-house.svg);
}

.advantage-two__middle-title::before {
  top: 19px;
  left: 24px;

  width: 32px;
  height: 38px;

  background-image: url(../img/icon-gift.svg);
}

.advantage-two__right-title::before {
  top: 23px;
  left: 18px;

  width: 40px;
  height: 35px;

  background-image: url(../img/icon-burger.svg);
}

.advantage-two__left-title {
  padding-top: 38px;
}

.advantage-two__description{
  padding: 18px 35px 0px 70px;
}

.advantage-two__description,
.advantage-four__description {
  font-size: 13px;
  letter-spacing: -0.2px;
  line-height: 20px;
}

.advantage-four__description {
  padding-left: 35px;
  padding-right: 35px;

  letter-spacing: -0.5px;
}

.advantage-two__right-description {
  padding-bottom: 38px;
}

.advantage-four {
  position: relative;

  background-color: #eeeeee;
}

.advantage-four::before,
.advantage-four::after {
  content: "";

  position: absolute;
  left: 20px;

  width: 280px;
  height: 1px;

  background-color: #d6d6d6;
}

.advantage-four::before {
  top: 168px;
}

.advantage-four::after {
  top: 336px;
}

.advantage-four__title {
  padding: 30px 20px 14px;

  letter-spacing: -1.7px;
}

.advantage-four__number {
  padding: 0px 20px 20px;
}

.advantage-four__description {
  padding-bottom: 27px;
}

@media (min-width: 768px) {
  .advantage-one,
  .advantage-two,
  .advantage-three,
  .advantage-four {
    width: 768px;
    margin: 0 auto;
  }

  .advantage-one,
  .advantage-two,
  .advantage-three,
  .advantage-four {
    display: flex;
  }

  .advantage-three {
    flex-direction: row-reverse;
  }

  .advantage-one__city,
  .advantage-three__bridge {
    width: 256px;
  }

  .advantage-one__title,
  .advantage-three__title {
    padding-top: 44px;

    font-size: 21px;
    letter-spacing: -1px;
  }

  .advantage-one__number,
  .advantage-three__number {
    padding-bottom: 26px;
  }

  .advantage-one__description,
  .advantage-three__description {
    padding: 0px 42px 44px;
  }

  .advantage-four::before,
  .advantage-four::after {
    display: none;
  }
}