.presentation {
  position: relative;

  width: 320px;
  margin: 0px auto;
}

.presentation__title {
  margin: 0px;
  padding: 15px 25px 30px;

  font-size: 18px;
  letter-spacing: -1.8px;
  text-transform: uppercase;
  text-align: center;
}

.presentation__description {
  margin: 0px;
  padding: 0px 20px 35px;

  font-size: 14px;
  text-transform: uppercase;
  line-height: 23px;
  text-align: center;
  letter-spacing: -0.8px;
}

.presentation__pic {
  margin-bottom: 138px;
}

.presentation__volume {
  position: absolute;
  bottom: 142px;
  left: 20px;

  height: 45px;
  width: 280px;

  background-color: #000000;
  opacity: 0.6;
}

.presentation::before {
  content: "";

  position: absolute;
  bottom: 159px;
  left: 78px;

  width: 9px;
  height: 9px;

  background-color: #ffffff;
  border-radius: 50%;

  z-index: 10;
}

.presentation::after {
  content: "";

  position: absolute;
  bottom: 162px;
  left: 43px;

  width: 234px;
  height: 3px;

  background-color: #ffffff;

  opacity: 0.2;
}

.console {
  position: absolute;
  bottom: 76px;
  left: 20px;

  height: 66px;
  width: 280px;

  background-color: #f2f2f2;
}

.console__one,
.console__two {
  position: relative;
}

.console__one::before {
  content: "";

  position: absolute;
  top: 23px;
  left: 25px;

  width: 20px;
  height: 20px;

  background-image: url(../img/icon-video-replay.svg);
  background-repeat: no-repeat;

  opacity: 0.6;
}

.console__two::before {
  content: "";
  
  position: absolute;
  top: 24px;
  left: 174px;

  width: 32px;
  height: 20px;

  background-image: url(../img/icon-video-subtitles.svg);
  background-repeat: no-repeat;

  opacity: 0.8;
}

.console__two::after {
  content: "";

  position: absolute;
  top: 24px;
  left: 237px;

  height: 20px;
  width: 20px;
  
  background-image: url(../img/icon-video-fullscreen.svg);
  background-repeat: no-repeat;

  opacity: 0.8;
}