.gallery h2,
.page-gallery__description,
.gallery h3 {
  margin: 0px;
  padding: 0px;

  text-transform: uppercase;
}

.gallery__info {
  width: 320px;
  margin: 0px auto;
}

.gallery .page-gallery__title {
  padding: 40px 50px 31px 50px;

  font-size: 18px;
  letter-spacing: -1.2px;
  text-align: center;
}

.gallery .page-gallery__description {
  position: relative;

  padding: 0px 25px 48px;

  font-size: 13px;
  line-height: 24px;
  text-align: center;
  letter-spacing: -0.4px;

  &::before {
    content: "";

    position: absolute;
    bottom: 22px;
    left: 0px;

    height: 1px;
    width: 100%;

    background-color: #e5e5e5;
  }
}

.open-spaces,
.vegetation,
.road,
.bridge {
  width: 320px;
  margin: 0px auto;

  display: flex;
  flex-direction: column;
}

.gallery__pic {
  padding: 0px 20px;
  
  order: -1;
}

.gallery .gallery__title {
  font-size: 13px;
  letter-spacing: -0.6px;

  padding: 20px 20px 10px;
}

.gallery__autor {
  margin: 0px;
  padding: 0px 20px 15px;

  font-size: 12px;
  letter-spacing: 0.5px;
}

.gallery__like {
  position: relative;

  padding: 0px 56px 44px;

  font-size: 15px;
  font-weight: bold;

  &::before {
    content: "";

    position: absolute;
    bottom: 22px;
    left: 0px;

    height: 1px;
    width: 100%;

    background-color: #e5e5e5;
  }

  &::after {
    content: "";
    
    position: absolute;
    top: -4px;
    left: 20px;

    height: 21px;
    width: 23px;

    background-image: url(../img/icon-like.svg);

    opacity: 0.3;
  }
}