.search {
  width: 320px;
  margin: 0 auto;
}

.search__title {
  margin: 0px;
  padding: 30px 55px 0px;

  font-size: 20px;
  text-transform: uppercase;
  letter-spacing: -1.7px;
}

.search__description {
  margin: 0px;
  padding: 20px 20px 35px;

  font-size: 14px;
  text-transform: uppercase;
  text-align: center;
  line-height: 24px;
  letter-spacing: -0.8px;
}

.search__button {
  display: flex;
  margin-bottom: 47px;
  margin-left: 21px;
  width: 278px;

  text-decoration: none;

  background-color: #81b3d2;

  &:hover,
  &:focus {
    background-color: #669ec0;

    outline: none;
  }

  &:active {
    background-color: #5496bd;
  }
}

.search__button span {
  padding: 18px 45px 18px;

  font-size: 20px;
  font-weight: bold;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: -0.8px;
}

.search__button:active span {
  opacity: 0.3;
}

.search__button--disabled {
  background-color: #e5e5e5;

  cursor: default;

  &:hover,
  &:focus,
  &:active {
    background-color: #e5e5e5;

    cursor: default;
  }
}

.search__button--disabled:active span {
  opacity: 1;
}

.search__map {
  position: relative;

  font-size: 0px;
}

.search__map::before {
  content: "";

  position: absolute;
  bottom: 195px;
  left: 146px;

  width: 17px;
  height: 17px;

  border: 5px solid #ffffff;
  border-radius: 50%;
  background-color: #81b4d4;

  z-index: 10;
}

.search__map-pic {
  margin-bottom: 33px;
}

@media (min-width: 768px) {
  .search__map-pic {
    margin-bottom: 10px;
  }
}