.page-intro {
  position: relative;

  width: 320px;
  margin: 56px auto 0px;

  font-size: 0px;

  background-image: url(../img/bg-back-mobile.jpg);
  background-position: 0px -230px;

  &::after {
    content: "";

    position: absolute;
    left: 0px;
    bottom: -31px;

    width: 320px;
    height: 57px;

    background-image: url(../img/bg-intro-triangle-mobile.svg);
    background-repeat: no-repeat;
  }
}

.page-intro__sedona {
  padding: 53px 47px 67px 44px;
}