  .intro {
  position: relative;
  
  width: 320px;
  margin: 56px auto 0px;

  background-image: url(../img/bg-back-mobile.jpg);

  &::before {
    content: "";

    position: absolute;
    bottom: 192px;

    width: 320px;
    height: 57px;

    background-image: url(../img/bg-intro-triangle-mobile.svg);
    background-repeat: no-repeat;
  }

  &::after {
    content: "";

    position: absolute;
    bottom: -16px;

    width: 320px;
    height: 37px;

    background-image: url(../img/bg-triangle.svg);
    background-repeat: no-repeat;
  }
}

.intro__pic {
  margin: 90px 0px 81px 6px;

  width: 282px;
  height: 214px;
}

.intro__title,
.intro__text {
  margin: 0px;

  text-transform: uppercase;
  text-align: center;

  background-color: $white;
}

.intro__title {
  padding-top: 41px;
  padding-right: 24px;
  padding-left: 24px;

  font-size: 17px;
  line-height: 26px;
  letter-spacing: -0.8px;
}

.intro__text {
  padding-top: 27px;
  padding-right: 20px;
  padding-bottom: 25px;
  padding-left: 20px;

  font-size: 14px;
  line-height: 26px;
  letter-spacing: -0.8px;
}

@media (min-width: 768px) {
  .intro {
    margin: 0px auto;

    width: 768px;

    background-image: url(../img/bg-back-tablet@1x.jpg);

    &::before,
    &::after {
      display: none;
    }
  }
}

.intro__pic {
  margin: 78px 0px 81px 51px;

  width: 460px;
  height: 350px;
}

.triangle {
  position: relative;

  &:after {
    content: "";

  position: absolute;
  right: -155px;
  bottom: -1px;

  width: 768px;
  height: 57px;

  background-image: url(../img/bg-intro-triangle-tablet.svg);
  background-repeat: no-repeat;
  }
}

.intro__title {
  padding: 55px 70px 0px 70px;

  font-size: 21px;

  letter-spacing: -1.6px;
}

.intro__text {
  padding: 29px 0px 50px;
}

