.reviews {
  width: 320px;
  margin: 0px auto;
}

.reviews__title {
  margin: 0px;
  padding: 40px 10px 0px;

  font-size: 18px;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: -1.5px;
}

.reviews__description {
  position: relative;
  
  margin: 0px;
  padding: 30px 5px 0px;

  font-size: 14px;
  text-transform: uppercase;
  text-align: center;
  line-height: 24px;
  letter-spacing: -0.8px;

  &::before {
    content: "";

    position: absolute;
    bottom: -17px;
    left: 0px;

    height: 1px;
    width: 320px;

    background-color: #e5e5e5;
  }
}

.review-user {
  position: relative;

  width: 320px;
  margin: 0px auto;
}

.review__title {
  margin: 0px;
  padding: 38px 10px 24px;

  font-size: 18px;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: -1.2px;
}

.review-contacts__title {
  padding-top: 7px;
}

.review-user__item {
  margin-bottom: 20px;
}

.review-user__label {
  padding: 0px 9px 0px 20px;

  font-size: 13px;
  text-transform: uppercase;
}

.review-user__input {
  line-height: 14px;
  
  border: none;
  outline: none;
}

::-webkit-input-placeholder {
  font-size: 16px;
  font-weight: bold;
  color: inherit;
}

::-moz-placeholder {
  font-size: 16px;
  font-weight: bold;
  color: inherit;
}

:-moz-placeholder {
  font-size: 16px;
  font-weight: bold;
  color: inherit;
}

:-ms-input-placeholder {
  font-size: 16px;
  font-weight: bold;
  color: inherit;
}

.review-user__name {
  padding: 22px 37px 22px 21px;

  width: 169px;

  background-color: #f2f2f2;
}

.review-user__surname,
.review-user__patronymic {
  padding: 22px 20px 22px 21px;

  width: 150px;

  background-color: #f2f2f2;
}

.review-user::after {
  content: "";

  position: absolute;
  bottom: -22px;
  left: 0px;

  height: 1px;
  width: 320px;

  background-color: #e5e5e5;
}

.review-contacts {
  position: relative;

  width: 320px;
  margin: 0 auto;
}

.review-contacts__title {
  padding-top: 25px;
}

.review-contacts__label {
  padding: 0px 39px 0px;

  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: -0.8px;
}

.review-contacts__phone,
.review-contacts__mail {
  margin: 11px 0px 16px 20px;
  padding: 23px 33px 20px 21px;

  width: 169px;

  background-color: #f2f2f2;
}

.review-contacts__label {
  position: relative;

  &::after {
    content: "";

    position: absolute;

    width: 58px;
    height: 58px;

    background-color: #e6e6e6;
  }
}

.review-contacts__label--phone::after {
  top: 28px;
  right: -61px;
}

.review-contacts__label--phone::before {
  content: "";

  position: absolute;
  top: 48px;
  right: -42px;

  width: 18px;
  height: 19px;

  background-image: url(../img/icon-phone.svg);
  background-repeat: no-repeat;

  z-index: 10;
}

.review-contacts__label--mail::after {
  top: 28px;
  right: -72px;
}

.review-contacts__label--mail::before {
  content: "";

  position: absolute;
  top: 49px;
  right: -51px;

  width: 16px;
  height: 14px;
  
  background-image: url(../img/icon-mail.svg);
  background-repeat: no-repeat;

  z-index: 10;
}

.review-contacts::after {
  content: "";

  position: absolute;
  left: 0px;
  bottom: -13px;

  height: 1px;
  width: 320px;

  background-color: #e5e5e5;
}

.review-impression {
  position: relative;

  width: 320px;
  margin: 0px auto;
}

.review-impression__title {
  padding-top: 35px;
  padding-bottom: 42px;

  letter-spacing: -1.7px;
}

[type="radio"],
[type="checkbox"] {
  display: none;
}

.review-impression__label {
  display: block;
  margin-bottom:49px;
  padding: 0px 10px 0px 95px;

  font-size: 17px;
  font-weight: bold;
  letter-spacing: -0.5px;

  cursor: pointer;
}

.review-impression__label {
  position: relative;

  &::before {
    content: "";

    position: absolute;
    top: -18px;
    left: 21px;

    width: 56px;
    height: 56px;

    background-color: #f2f2f2;
    border-radius: 50%;
  }

  &::after {
    content: "";

    position: absolute;
    top: -1px;
    left: 38px;

    display: none;
    width: 22px;
    height: 22px;

    background-image: url(../img/icon-shape.png);
    background-repeat: no-repeat;
  }
}

input[id="review-impression__positively"]:checked + .review-impression__label::after,
input[id="review-impression__negatively"]:checked + .review-impression__label::after,
input[id="review-impression__difficult"]:checked + .review-impression__label::after {
  display: block;
}

.review-impression::after {
  content: "";

  position: absolute;
  left: 0px;
  bottom: -46px;

  height: 1px;
  width: 320px;

  background-color: #e5e5e5;
}

.review-attraction {
  position: relative;

  width: 320px;
  margin: 0px auto;
}

.review-attraction__title {
  padding: 15px 0px 34px 0px;

  line-height: 26px;
  letter-spacing: -1.5px;
}

.review-attraction__label {
  position: relative;
  padding: 0px 20px 0px 100px;
  margin-bottom: 60px;
  display: block;

  font-size: 17px;
  font-weight: bold;
  letter-spacing: -0.8px;

  cursor: pointer;

  &::before {
    content: "";

    position: absolute;
    top: -17px;
    left: 20px;

    width: 56px;
    height: 56px;

    background-color: #f2f2f2;
    border-radius: 5px;
  }

  &::after {
    content: "";

    position: absolute;
    top: 3px;
    left: 37px;

    display: none;
    width: 23px;
    height: 16px;

    background-image: url(../img/icon-tick.svg);
    background-repeat: no-repeat;
  }
}

input[id="review-attraction__bridge"]:checked + .review-attraction__label::after,
input[id="review-attraction__mountain"]:checked + .review-attraction__label::after,
input[id="review-attraction__park"]:checked + .review-attraction__label::after,
input[id="review-attraction__rock"]:checked + .review-attraction__label::after {
  display: block;
}

.review-attraction::after {
  content: "";

  position: absolute;
  left: 0px;
  bottom: -45px;

  height: 1px;
  width: 320px;

  background-color: #e5e5e5;
}

.review-opinion {
  width: 320px;
  margin: 0px auto;
}

.review-opinion__title {
  padding-top: 7px;
  padding-bottom: 18px;
}

.review-opinion__text {
  margin-bottom: 47px;
  margin-left: 18px;
  padding: 17px 28px 91px 22px;

  width: 230px;

  font-size: 18px;
  font-weight: bold;
  line-height: 26px;
  letter-spacing: -1px;

  background-color: #f2f2f2;
  border: none;
  outline: none;
}

.review__button {
  width: 320px;
  margin: 0px auto;
}

.review__send {
  margin: 0px 18px 74px;
  padding: 19px 10px 16px;

  width: 280px;

  font-size: 20px;
  font-weight: bold;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: -0.8px;

  background-color: #81b3d2;
  border: none;
  outline: none;

  cursor: pointer;
}