.page-footer {
  width: 320px;
  margin: 0px auto;
}

.page-footer__hash-tag {
  text-align: center;
}

.page-footer p {
  margin: 0px;
  padding: 0px;
}

.social__items {
  margin: 0px;
  padding: 0px;

  list-style: none;
}

.page-footer .page-footer__hash-tag {
  padding: 0px 15px 36px;

  font-size: 21px;
  font-weight: bold;
  line-height: 26px;
  text-transform: uppercase;
  letter-spacing: -1.2px;
}

.social {
  display: flex;
  flex-direction: row;
  margin-left: 85px;

  font-size: 0px;
}

.social__link {
  position: relative;

  margin-right: 6.5px;
  width: 46px;
  height: 48px;

  background-color: #81b3d2;

  &:hover,
  &:focus {
    background-color: #669ec0;

    outline: none;
  }

  &:active {
    background-color: #5496bd;
  }
}

.social__link::before {
  content: "";

  position: absolute;
  
  background-repeat: no-repeat;
}

.social__link:active::before {
  opacity: 0.3;
}

.social-link__twitter::before {
  top: 16px;
  left: 14px;
  
  width: 18px;
  height: 14px;

  background-image: url(../img/icon-twitter.svg);
}

.social-link__fb::before {
  top: 14px;
  left: 18px;

  width: 9px;
  height: 18px;

  background-image: url(../img/icon-facebook.svg);
}

.social-link__youtube::before {
  top: 17px;
  left: 13px;
  
  width: 20px;
  height: 14px;

  background-image: url(../img/icon-youtube.svg);
}

.page-footer .page-footer__copyrite {
  position: relative;

  padding: 44px 95px 27px;

  font-size: 13px;
  line-height: 26px;
  text-transform: uppercase;
  letter-spacing: -0.5px;
}

.page-footer__copyrite::before {
  content: "";

  position: absolute;
  top: 41px;
  right: 96px;

  width: 27px;
  height: 34px;

  background-image: url(../img/logo-htmlacademy.svg);
  background-repeat: no-repeat;
}

@media (min-width: 768px) {
  .page-footer {
    display: flex;
    align-items: center;

    width: 768px;
    min-height: 100px;
    margin: 0px auto;
  }

  .page-footer .page-footer__hash-tag,
  .page-footer .social,
  .page-footer .page-footer__copyrite {
    margin: 0px;
    padding: 0px;
  }

  .page-footer .page-footer__hash-tag {
    margin-left: 60px;
    padding-right: 110px;
  }

  .page-footer .social {
    padding-right: 110px;
  }

  .page-footer__copyrite::before {
    top: -5px;
    right: -40px;
  }
}