.nav {
  position: relative;

  width: 320px;
  margin: 0px auto;

  background-color: $lightblue;
}

.nav__items {
  position: relative;
  margin: 0;
  padding: 0;

  list-style: none;

  &::after {
    content: "";

    position: absolute;
    bottom: 0px;

    width: 100%;
    height: 7px;

    background-image: url(../img/layer.png);
  }
}

.nav__items a[href] {
  &:hover,
  &:focus {
    background-color: #669ec0;
  }

  &:active {
    background-color: #5496bd;

    opacity: 0.3;
  }
}

.nav__link {
  display: block;

  padding: 18px 25px;

  font-weight: bold;
  font-size: 16px;
  color: $white;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;

  &:focus {
    outline: none;
  }
}

.nav__toggle {
  position: absolute;

  padding: 0px;

  border: none;
  outline: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.menu--close::after {
  content: "";

  position: absolute;
  left: 792px;
  top: 18px;

  width: 23px;
  height: 22px;

  background-image: url(../img/icon-menu.svg);

  cursor: pointer;
  z-index: 2;
}

.menu--close:hover::after,
.menu--close:focus::after {
  opacity: 0.6;
}

.menu--close:active::after {
  opacity: 0.3;
}

.nav .menu--open::before {
  content: "";

  position: absolute;
  top: 17px;
  left: 277px;
  
  width: 23px;
  height: 22px;

  background-image: url(../img/icon-cross.svg);

  cursor: pointer;
  z-index: 2;
}

.nav .menu--open:hover::before,
.nav .menu--open:focus::before {
  opacity: 0.6;
}

.nav .menu--open:active::before {
  opacity: 0.3;
}

.nav__item:not(:last-child) .nav__link {
  border-bottom: 1px solid #678fa8;
}

.nav__item:not(:first-child) .nav__link {
  border-top: 1px solid #9ac2db;
}

.nav--closed {
  display: none;
}

.nav--open {
  display: block;
}

@media (min-width: 768px) {

  .nav__toggle {
    display: none;
  }

  .page-header__logo {
    top: 0px;
    background-image: url(../img/logo-sedona-tablet.svg)
  }

  .nav {
    width: 768px;
    margin: 0 auto;

    background-color: #ffffff;
  }

  .nav__items::before,
  .nav__item:last-child::after {
    display: none;
  }

  .nav__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
  }

  .nav__items {
    padding: 19px 0px 20px;
    background: none;
  }

  .nav__items::after {
    background: none;
  }

  .nav__item {
    border: none;
  }

  .nav__item:first-child {
    padding-left: 48px;
    padding-right: 77px;
  }

  .nav__item:nth-child(2) {
    padding-right: 210px;
  }

  .nav__item:last-child {
    padding-left: 50px;
    padding-right: 28px;
  }

  .nav__link {
    padding: 0px;

    font-size: 14px;
    color: #000000;
    letter-spacing: -1.3px;
  }

  .nav__items a[href],
  .nav__items a[href] {
    &:hover,
    &:focus {
      color: #81b3d2;
      background-color: #ffffff;
    }
    &:active {
      color: #b2b2b2;
    }
  }

  .nav__item:not(:first-child) .nav__link,
  .nav__item:not(:last-child) .nav__link {
    border: none;
  }

  .menu--close::after {
    display: none;
  }
}

